* {
  font-family: 'Roboto', sans-serif; }




header {
    position: relative;
    height: 50px; }
nav {
    height: 50px;
    background: #212121;
    position: fixed;
    width: 100%;
    z-index: 2; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    .menu_link {
        font-weight: 300;
        font-size: 15px;
        color: #FFFFFF; }
    .menu_item {
        border-right: 2px dotted black;
        padding-right: 28px; } }

.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 87px;
    background: rgba(0, 0, 0, 0.6);

    .subheader_logo {
        display: block;
        margin-top: 16px;
        max-width: 100%; }
    .subheader_text1 {
        margin-top: 6px;
        font-weight: 400;
        font-size: 13px;
        color: #FFFFFF;
        opacity: 0.5; }
    .subheader_text2 {
        margin-top: 23px;
        font-weight: 300;
        font-size: 12px;
        color: #FFFFFF;
        text-align: right; }
    .subheader_call {
        display: block;
        margin-top: 8px;
        font-weight: 400;
        font-size: 16px;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 22px;
        text-align: right; }
    .subheader_btn {
        margin-top: 21px;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        width: 172px;
        height: 46px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px; } }

.promo {
    min-height: 800px;
    background: url('../img/bg/uber_bg.png') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;
    .promo_header {
        font-weight: 700;
        font-size: 60px;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 60px;
        margin: 0;
        text-align: center; }
    .promo_subheader {
        font-weight: 700;
        font-size: 36px;
        line-height: 36px;
        text-transform: uppercase;
        color: #FFFFFF;
        margin: 0;
        margin-top: 22px;
        text-align: center; }
    .promo_text {
        font-size: 18px;
        line-height: 24px;
        color: #FFFFFF;
        text-align: center;
        font-weight: 400;
        margin-top: 16px; }
    .promo_btn {
        display: block;
        margin: 118px auto 0 auto;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        width: 259px;
        height: 63px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px; } }




.page2 {
    padding: 18px 0 64px 0; }

.title {
    margin: 0 auto;
    font-weight: normal;
    font-size: 11px;
    text-transform: uppercase;
    color: #FFFFFF;
    mix-blend-mode: normal;
    padding: 5px 12px;
    width: 115px;
    height: 27px;
    background: #000000;
    text-align: center; }

.bigtitle {
    margin-top: 33px;
    font-style: normal;
    font-weight: bold;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #222222; }

.subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    text-align: center;
    color: #222222;

    .page2_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px;
        .page2_round {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 116px;
            height: 116.15px;
            background: #1EACC7;
            border-radius: 100%;
            margin-right: 26px; }
        .page2_descr {
            width: 343px; }
        .page2_subtitle {
            font-style: normal;
            font-weight: normal;
            font-size: 17px;
            line-height: 21px;
            color: #1EACC7; }
        .page2_text {
            color: #202020;
            font-size: 15px;
            font-weight: 300;
            line-height: 21px; } } }

.page3 {
    padding: 48px 0 80px 0;
    .title {
        margin: 0 auto;
        font-weight: normal;
        font-size: 11px;
        text-transform: uppercase;
        color: #FFFFFF;
        mix-blend-mode: normal;
        padding: 5px 12px;
        width: 115px;
        height: 27px;
        background: #000000;
        text-align: center; }
    .bigtitle {
        margin-top: 33px;
        font-style: normal;
        font-weight: bold;
        font-size: 38px;
        text-align: center;
        text-transform: uppercase;
        color: #222222; }
    .subtitle {
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        text-align: center;
        color: #222222; } }


